
.seccion {

    padding: 5rem 0;        // Establecemos el relleno interior de los elementos <div> de las secciones
    display: none;          // Ocultamos los elementos <div> cuya clase es "seccion"

    .text-center{
        text-align: center; // Centramos el texto de los elementos <p> que tienen la clase text-center
    }
}

.mostrar {
    display: block;         // Establecemos que el elemento ocupe el 100% del espacio para que cada alerta se vea en una linea diferente
}

.ocultar {
    display: none;         // Ocultamos los elementos <div> cuya clase es "ocultar" 
}

