@use 'base/variables' as v;     //Importamos _variables.scss y le damos como alias v
@use 'base/mixins' as m;        //Importamos _mixins.scss y le damos como alias m

.formulario {
    margin-top: 5rem;           // Establecemos  la separacion superior entre el <form> y la etiqueta superior.
    width: 100%;                // Establecemos el ancho del <div> del formulario.
}

.campo {
    display: flex;              // Habilitamos las propiedades del flexbox
    margin-bottom: 2rem;        // Establecemos la separación inferior entre los  <div> de los campos de email y password.
    align-items: center;        // Alineamos todos los elementos dentro del elemento .campo (<label> e <input>) respecto al eje transversal, es decir, horizontalmente.

    label{
        flex: 0 0 10rem;        // flex (grow, shrink, basis). 
                                // 1. Grow es el factor de crecimiento, al ser cero el elemento no tedrá la habilidad de crecer en el espacio disponible
                                // 2. Shrink es el factor de compresión. Determina cuanto se debe comprimir un elemento cuando no hay espacio suficiente. El valor por defecto es 1.
                                // 3. Basis es el tamaño inicial del elemento <label>

    }

    input{
        flex: 1;                            // Hacemos que el <input> tome el resto del tamaño disponible de los elementos dentro de su <div> (cuya clase es "campo", ocupado por <label> y <input>)
        border: none;                       // Eliminamos el borde del <input>
        padding: calc(v.$separacion / 4);   // Establecemos el relleno alrededor del contenido de un elemento dentro de los bordes definidos. calc nos permite operar dentro de la propiedad.
        border-radius: 1rem;                // Redondeamos los bordes del input

        &:disabled {
            background-color: lighten($color: v.$negro, $amount: 30%); // Establecemos color de fondo. Usamos la funcion de SASS lighten indicando (color, % de aclarado)
            color: v.$blanco;                                          // Establecemos el color del texto
            cursor: not-allowed;                                       // Eliminamos el puntero para remarcar que el campo esta desabilitado
        }

    }
}