@use 'base/variables' as v;             //Importamos _variables.scss y le damos como alias v

.alerta {                               // Estilos para cuando el <div> tenga la clase alerta
    padding: calc(v.$separacion / 4);    // Establecemos el relleno del <div>
    color: v.$blanco;                   // Establecemos el color del texto del <div>
    width: 100%;                        // Establecemos el ancho del <div>
    display: block;                     // Establecemos que el elemento ocupe el 100% del espacio para que cada alerta se vea en una linea diferente
    margin-bottom: 2rem;                // Para separar cada linea de alerta que tengamos
    text-align: center;                 // Centramos el texto
    font-weight: v.$bold;               // Establecemos el grosor del texto.
    text-transform: uppercase;          // Transformamos el texto a mayusculas


    &.error {                           // Estilos para cuando el <div> ademas de la clase alerta tenga la clase error
        background-color: v.$rojo;      // Establecemos el color de fondo del <div>

    }     
    
    &.exito {                           // Estilos para cuando el <div> ademas de la clase alerta tenga la clase exito
        background-color: v.$verde;     // Establecemos el color de fondo del <div>

    }   


}