@use 'base/variables' as v;     //Importamos _variables.scss y le damos como alias v

.citas {
    list-style: none;           // Quitamos la viñeata que tiene los elementos <ul> por defecto
    padding: 0;                 // Espacio dentro del elemento .citas, es decir, el <ul>

    li {
        padding: 1rem;                      // Espacio dentro del elemento <li>
        border-top: 1px solid v.$blanco;    // Estilo del borde superior del elemento <li>

        p {
            font-weight: v.$bold;           // Grosor del texto
            color: v.$azul;                 // Color del texto

        }

        span,
        .servicio {
            font-weight: v.$regular;           // Grosor de la fuente
            color: v.$blanco;                  // Color del texto
        }
    }
}

.servicios {
    @extend .citas;

    .acciones {
        margin: 0;                              // Espacio alrededor del elemento por fuera
    }

    .boton,
    .boton-eliminar {
        margin: 0;                              // Espacio alrededor del elemento por fuera
    }
}

