@use 'base/variables' as v;         //Importamos _variables.scss y le damos como alias v
@use 'base/mixins' as m;            //Importamos _mixins.scss y le damos como alias m

.paginacion {
    padding: v.$separacion 0;       // Establecemos el relleno del <div> que contiene a los botones de paginación

    @include m.telefono {           // Importamos el mixin del @media Query 
        display: flex;              // Establecemos que los contenedores hijos de <div>, los <button>, se comviertan en flexibles adaptandose al tamaño y espacio disponible
        justify-content: space-between; // Establecemos que los elementos hijo de <div> se distribuyan uno al inicio del contenedor y otro al final dejando un espacio igual entre cada elemento
                                        // y el eje principal que esta situado en el centro del espacio total
    }      
    
    button {
        margin: 0 0 1rem 0;         // Establecemos la separación desde el exterior del elemento (arriba derecha abajo izquierda)
    }
}

.ocultar {
    visibility: hidden;             // Oculta el elemento manteniendo su posición en el DOM (esta propiedad es como display:none pero ésta no mantiene la posición en el DOM con lo que 
                                    // en nuestro caso el elemento se colocaría a la izquierda si tiene la posibilidad y no lo estuviera ya)
}