@use 'base/variables' as v;     //Importamos _variables.scss y le damos como alias v
@use 'base/mixins' as m;        //Importamos _mixins.scss y le damos como alias m

.acciones{
    margin-top: v.$separacion;              // Establecemos la separación entre el <div> de acciones y el boton de iniciar sesion del formulario
    @include m.tablet {
        display: flex;                      // Hacemos que los enlaces se muestren en la misma linea uno a continuación de otro.
        justify-content: space-between;     // Hacemos que los enlaces se muestren en los extremos.
        align-items: center;                // Alineamos verticalmente

    }
    a {
        color: v.$blanco;                           // Establecemos el color del texto de los enlaces
        display: block;                             // Hacemos que cada enlace ocupe todo el espacio forzando el salto de linea
        margin-bottom: calc(v.$separacion / 2);     // Establecemos la separación inferior entre los enlaces.
        text-align: center;                         // Centramos el texto de los enlaces
    }
}