@use 'base/variables' as v;     //Importamos _variables.scss y le damos como alias v

.contenido-resumen {
    p {
        margin-bottom: 1rem;            // Margen inferior por fuera del elemento
    }

    span {
        color: v.$azul;                 // Color de la fuente
        font-weight: v.$bold;           // Grosor de la fuente
        font-size: 2.4rem               // Tamaño de la fuente    
    }
}

.contenedor-servicio {
    padding: 3rem 0;                    // Espacio dentro del elemento (arriba y abajo, izqda y dcha)
    border-bottom: 1px solid v.$gris;   // Estilos para el borde inferior del <div>

    &:last-of-type {
        margin-bottom: v.$separacion;   // Margen inferior por fuera del elemento
    }

}