@use 'variables' as v;                          //Importamos _variables.scss y le damos como alias v

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: v.$blanco;
    text-align: center;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
p {
    color: v.$blanco;
    font-size: 2rem;
}

.nombre-pagina {
    text-align: center;                         // Establecemos centrar el texto horizontalmente dentro de la linea.
    margin: 5rem 0;                             // El margin es el relleno que se crea alrededor de un elemento hacia afuera de sus bordes definidos (5rem arriba y abajo y 0 a izqda y dcha).
    font-weight: v.$bold;                       // Establecemos el ancho de la fuente definido e importado en variables.scss alias v.
}

.descripcion-pagina {
    text-align: center;

}
