@use 'base/variables' as v;     //Importamos _variables.scss y le damos como alias v
@use 'base/mixins' as m;        //Importamos _mixins.scss y le damos como alias m

.contenedor-app {
    @include m.tablet {                         //  Importamos de mixins.scss (alias m) el mixin cuyo nombre es tablet
        display: grid;                          //  Habilitamos el grid para distribuir los elementos en los ejes horizontal y vertical simultaneamente
        grid-template-columns: repeat(2, 1fr);  //  Definiimos el numero de columnas que tendra nuestra rejilla del grid y su tamaño. En nuestro caso 2 columnas iguales
        min-height: 100vh;                      //  Definimos la altura mínima del contenedor (100vh significa que establecemos como tamaño el 100% del alto de la ventana 
                                                //  (v:ventana visible o visible viewport, h:alto o height)
    }

    .imagen {

        height: 20rem;                          //  Establecemos la altura de la imagen
        background-image: url(../img/1.jpg);    //  Establecemos de fondo del <div> la imagen que se encuentra en la ruta especificada entre parentesis
        background-size: cover;                 //  Especificamos el espacio que ocupa la imagen. Cover hace que la imagen sea renderizada ocupando todo el elemento conservando su tamaño.
        background-position: center center;     //  Definimos la posicion de la imagen. En este caso centramos la imagen horizontal y verticalmente
        @include m.tablet {                     //  Importamos de mixins.scss (alias m) el mixin cuyo nombre es tablet
            height: auto;                       //  Establecemos que la imagen tome de alto todo el espacio disponible.
        }
    }

    .app {

        width: 95%;                             // Establecemos que como ancho se tomo un 95% del espacio definido
        margin: 0 auto;                         // Establecemos que los margenes superior e inferior sean cero y el auto hace que el espacio que sobre de ancho se reparta en dos partes iguales
                                                // quedando el elemento centrado.
        padding: 3rem 0;                        // El padding es el relleno que se crea alrededor del contenido de un elemento dentro de los bordes definidos. 3rem arriba y abajo 0 a izqda y dcha
        @include m.tablet {                     // Importamos el mixin tablet ( Media Query para pantallas mayores de 768px)    
            overflow: scroll;                   // En caso de desbordamiento (overflouw) se habilitará una barra de desplazamiento al elemento para que el usuario se pueda desplazar por el
                                                // contenido
        }
    }
}

.barra {
    margin: v.$separacion 0;                    // Establecemos la serparación alrededor del elemento (arriba y abajo, izqda y dcha)

    @include m.tablet {                         // Importamos el Media Query Tablet (para anchos mayores a 768px)
        display: flex;                          // Habilitamos las propiedades de flex
        justify-content: space-between;         // Distribuimos el espacio de los elementos en los bordes derecho e izquierdo
        align-items: center;                    // Alineamos verticalmente los elementos del contenedor .barra (<div>)
    }

    .boton {
        @include m.boton(v.$negro, v.$blanco);  // Importamos el mixin de boton que toma por parametro (color de fondo, color de texto)
        margin: 0;                              // Establecemos la separación alrededor del elemento
    }
}

.barra-servicios {
    margin-bottom: v.$separacion;               // Establecemos el margen inferior

    @include m.tablet {                         // Importamos el Media Query Tablet (para anchos mayores a 768px)
        display: flex;                          // Habilitamos las propiedades de flex
        justify-content: space-between;         // Distribuimos el espacio de los elementos en los bordes derecho e izquierdo
    }

    .boton {
        margin: 0 0 1rem 0;                     // Establecemos los margenes por fuera del elemento
    }
}