@use 'base/variables' as v;     //Importamos _variables.scss y le damos como alias v
@use 'base/mixins' as m;        //Importamos _mixins.scss y le damos como alias m

.listado-servicios {
    display: grid;              // Habilitamos el grid para poder usar la propiedad gap
    gap: 2rem;                  // Establecemos el espacio entre filas y columnas de una cuadricula creada con display grid. Acepta 2 valores (filas, columnas) en unidades
                                // absolutas (píxeles, cms, etc.) o en unidades relativas (porcentaje, em, rem, etc.)

    @include m.tablet {             // Mixin de media query para dispositivos con tamaño tablet o superior
        @include m.grid(2, 2rem);   // Mixin para la creación de columnas
    }

    .servicio {
        background-color: v.$blanco;                   // Establecemos el color de fondo
        padding: 3rem;                                 // Establecemos el espacio en el interior del elemento <div>
        border-radius: 1rem;                           // Establecemos esquinas redondeadas para el <div>
        transition-property: transform;                // Espedificamos que propiedad o propiedades deben ser animadas durante una transición suave entre dos estados de un elemento
        transition-duration: 300ms;                    // Establecemos la duración de la transición entre estados del elemento <div> 
        text-align: center;                            // Centramos el texto del elemento <div>

        &.seleccionado {
            background-color: v.$azul;                 // Establecemos el color de fondo
            p {
                color: v.$blanco                       // Establecemos el color del <p>
            }
        }

        &:hover {                                      // Propiedades que se aplican al pasar el raton sobre el <div> de servicio
            transform: scale(1.1);                     // Hacemos que el <div> aumente de tamaño un 10%
        }

        p {
            color: v.$negro;                           // Establecemos el color del <p>
            margin: 0 0 calc(v.$separacion / 4) 0;     // Establecemos la separación entre parrafos
        }

        .precio-servicio { 
            color: v.$azul;             // Establecemos el color del <p> del precio
            font-weight: v.$bold;       // Establecemos el grosor de la fuente
            font-size: 3rem;            // Establecemos el tamaño de la fuente
            margin: 0;                  // Eliminamos el espacio en el exterior del <p> del precio
        }  
    }
}