@use 'base/variables' as v;         //Importamos _variables.scss y le damos como alias v
@use 'base/mixins' as m;            //Importamos _mixins.scss y le damos como alias m

.tabs {

    @include m.tablet {              // Importamos el mixin del @media Query 
        display: flex;               // Establecemos que los contenedores hijos de <nav>, los <button>, se comviertan en flexibles adaptandose al tamaño y espacio disponible

    }

    button {
        background-color: v.$azul;   // Establecemos el color de fondo los botones
        border: none;                // Eliminamos el borde de los botones   
        color: v.$blanco;            // Establecemos el color del texto   
        padding: 1rem;               // Establecemos el relleno interior de los botones  
        display: block;              // Establecemos que los elementos se coloquen uno de bajo del otro. Se colocara cada <button> en una linea
        width: 100%;                 // Hacemos que cada boton en cada linea tome el 100% del espacio disponible en la linea   
        margin-bottom: 1rem;         // Establecemos una separacion inferior entre el elemento y el siguiente desde el exterior del elemento.   
        text-transform: uppercase;   // Transformamos el texto a mayusculas   
        font-weight: v.$bold;        // Establecemos el grosor de la fuente
        transition: background-color 300ms ease;  // Establecemos una transicion entre propiedades backgrouncolor de 300 ms tipo ease (la transicion empieza lento, aumenta la velocidad a 
                                                  // medida que avanza y luego desacelera dando sensación de suavidad la transición.  
        
        &:hover {                    // Pseudo-selector que nos permite que se den estilos cuando se pasa el cursor sobre el elemento, <button> en nuestro caso.
            cursor: pointer;         // Establecemos que se muestre un puntero
            background-color: darken(v.$azul, 10%);  // Establecemos que se oscurezca el color del primer parametro el porcentaje del segundo parametro
        }

        &.actual {
            background-color: v.$blanco; // Establecemos el color de fondo los botones
            color: v.$azul;              // Establecemos el color del texto
        }
    }
}